.head-img {
 width: 11rem;
 cursor: pointer;
}

.header {
 padding: 20px 20px;
 display: flex;
 justify-content: space-between;
 gap: 1rem;
}

.nav-items {
 display: flex;
 list-style: none;
 cursor: pointer;
 gap: 4rem;
 font-weight: bold;
 color:#fff;
 font-size: 1.1rem;
}

.join-now {
 width:100px;
 height:50px;
 cursor: pointer;
 border-radius: 30px;
 background: var(--cf-blue);
 border: 0;
 color:#fff;
 font-weight: bold;
 font-size: 0.8rem;
}

.join-now:hover {
 background-color: black;
 color:#fff;
 border: 0;
 transition: 0.5s all;

}

.nav-items > li:hover {
 text-decoration: underline;
 transition: all 0.5s;
 opacity:80%
}


