.footer {background-image: url(data:image/png;base64,);
mix-blend-mode: overlay;
background-color: #000;
display: flex;
flex-direction: row;
gap: 6rem;
overflow: hidden;
color:white;
justify-content: space-between;
}


.footer{
 padding:40px 40px;
 gap: 4rem;
}
.footer-img {
 width:12rem;
 cursor: pointer;
}

.instagram > a >img {
 width: 1.5rem;
 height: 1.5rem;
 cursor: pointer;
}
.facebook > a > img  {
   width: 1.5rem;
   height: 1.5rem;
   cursor: pointer;
  }

.mid-img {
 gap:4rem;
}
.mid-f{
 display: flex;
 gap:2rem;
 flex-direction: row;
 margin-top: 20px;
}
.mid-f > button {
  padding: 5px 5px;
  background: none;
  border: 0;
}

.mid-f>button:hover {
  background-color: var(--cf-blue);
  opacity: 100%;
  transition: all 0.5s;
  fill: black;
}
.right-f {
  margin-top:21px ;
}

